@media (max-width: 767px) {
  .footer__nav-list {
    max-width: none;
    display: flex;
    flex-direction: column;
  }
  .footer__nav-list--first {
    grid-column: 1/2;
    margin-top: 28px;
  }
  .footer__nav-list--second {
    grid-row: 1/3;
    grid-column: 2/3;
  }
}

.footer__nav-item a {
  display: inline-block;
  vertical-align: middle;
  color: #ffffff;
  margin-right: 40px;
  font-size: 14px;
  line-height: 16px;
  padding-bottom: 10px;
  padding-top: 10px;
}
@media (max-width: 1199px) {
  .footer__nav-item a {
    margin-right: 30px;
  }
}
@media (max-width: 767px) {
  .footer__nav-item a {
    margin-bottom: 30px;
  }
}

.nojs.tabs__block {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
}
@media (max-width: 1199px) {
  .nojs.tabs__block--active {
    flex-direction: column;
  }
}

